	module.exports = {"data":{"markdownRemark":{"html":"<h3>JavaScript and the Web</h3>\n<p><a href=\"https://twitter.com/dan_abramov/status/981712092611989509\">React Lifecycle Diagram | Dan Abramov</a></p>\n<p>Dan shared a really great illustration of the React lifecycle methods on twitter that can help you internalize how they work if you’re unfamiliar.</p>\n<p><a href=\"https://martinfowler.com/articles/201803-refactoring-2nd-ed.html\">Announcing the Second Edition of “Refactoring”</a></p>\n<p>The upcoming second edition of <a href=\"https://amzn.to/2IzhmhS\">Refactoring</a> will have it’s code examples written in JavaScript.  I’d love to see more architecture-level books written for JavaScript developers.  I don’t think we have great definitive resources for those questions yet, and rewriting a classic with JS examples is a great first step.</p>\n<p><a href=\"https://developers.google.com/web/updates/2018/04/present-web-pages-to-secondary-attached-displays\">Present web pages to secondary attached displays | Google Developers</a></p>\n<p>This is a small thing, but I thought Chrome’s experimental support for external display controls was cool, and may eventually lead to me actually using one of the many JS based presentation tools instead of Keynote.</p>\n<h3>Tech</h3>\n<p><a href=\"https://www.citylab.com/transportation/2018/03/former-uber-backup-driver-we-saw-this-coming/556427/\">Former Uber Backup Driver: We Saw This Coming | Citylab</a></p>\n<p>May be some sour grapes here, but the piece raises some interesting questions about the transition from our current state to a self-driving car future, and whether a <a href=\"https://www.caranddriver.com/features/path-to-autonomy-self-driving-car-levels-0-to-5-explained-feature\">Level 3</a> self driving car is even a feasible goal.</p>\n<p><a href=\"https://www.joelonsoftware.com/2018/04/06/the-stack-overflow-age/\">The Stack Overflow Age | Joel Spolsky</a></p>\n<p>Stack Overflow had a huge influence on my early days learning JavaScript.  I love seeing the history here from one of the founders.</p>\n<h3>Humor</h3>\n<p><a href=\"https://www.facebook.com/photo.php?fbid=10156523301334739&#x26;set=a.10150885243919739.471335.523144738&#x26;type=3&#x26;theater\">Returning To The Empress Hotel | Nick Burchill (Facebook)</a></p>\n<p>One of the funnier stories I’ve read on the internet in a while.</p>","frontmatter":{"title":"Weekly Links: April 7th","keywords":"react refactoring uber","category":"opinion","date":"2018/04/07","path":"/2018/04/07/weekly-links-04-07-18/","layout":"weekly-links","hideFooter":null},"fields":{"slug":"/2018/04/07/weekly-links-04-07-18/"}}},"pathContext":{"slug":"/2018/04/07/weekly-links-04-07-18/","relatedPosts":[{"path":"/2012/11/09/bayesian-witch-hunt","data":{"title":"Bayesian Witch Hunt","path":"/2012/11/09/bayesian-witch-hunt","description":"A nerd tribute from a friend","date":"2012-11-09T12:00:00+00:00"}},{"path":"/2012/12/29/i-hate-computing-ecosystems","data":{"title":"I hate computing ecosystems","path":"/2012/12/29/i-hate-computing-ecosystems","description":"Why does nothing work together?","date":"2012-12-29T23:06:00+00:00"}},{"path":"/2012/12/25/medium-the-end-of-history-and-the-last-website","data":{"title":"Medium: The end of History and the last website","path":"/2012/12/25/medium-the-end-of-history-and-the-last-website","description":"Is there any use in personal websites anymore?","date":"2012-12-25T21:00:00+00:00"}}]}}

